import React from 'react'

const title = () => {
  return (
    <div className='header'>
        <div >
        <h2 className='title-one'>GET</h2>
        <h2 className='title'>SHIT</h2>
        <h2 className='title'>DONE.</h2>
        </div>
        <img className='user' src='data:image/jpeg;base64,' alt='person'></img>
    </div>
  )
}

export default title